'use client';

import React, { useState, useEffect } from 'react';
import { useSearchParams, useRouter } from 'next/navigation';
import {
  Box,
  Typography,
  Breadcrumbs,
  Link,
  Accordion,
  AccordionSummary,
  AccordionDetails,
  IconButton,
  ThemeProvider,
} from '@mui/material';
import ExpandMoreIcon from '@mui/icons-material/ExpandMore';
import ArrowBackIosNewIcon from '@mui/icons-material/ArrowBackIosNew';
import { styled } from '@mui/system';
import theme from '@/app/theme';

// ==========================
// Styled Components
// ==========================
const PageContainer = styled(Box)(({ theme }) => ({
  maxWidth: 420,
  margin: '0 auto',
  backgroundColor: theme.palette.background.paper,
  minHeight: '100vh',
}));

const HeaderBar = styled(Box)(({ theme }) => ({
  backgroundColor: theme.palette.primary.main,
  color: theme.palette.primary.contrastText,
  display: 'flex',
  alignItems: 'center',
  padding: '0 16px',
  height: 56,
  fontWeight: 600,
}));

const BackBar = styled(Box)(({ theme }) => ({
  display: 'flex',
  alignItems: 'center',
  gap: '8px',
  padding: '12px 16px',
  borderBottom: `1px solid ${theme.palette.divider}`,
}));

const SectionHeader = styled(Box)(({ theme }) => ({
  padding: '16px',
  borderBottom: `3px solid ${theme.palette.primary.main}`,
}));

const AccordionStyled = styled(Accordion)(({ theme }) => ({
  border: `1px solid ${theme.palette.divider}`,
  margin: '8px 16px',
  borderRadius: theme.shape.borderRadius,
  '&:before': { display: 'none' },
}));

// ==========================
// FAQ 資料（可自行補充）
// ==========================
const SUPPORT_MAP = {
  purchase: {
    name: '購票',
    desc: '購票及付款方式說明',
    faqs: {
      購票流程說明: '完整購票流程：登入 → 選擇場次 → 選位 → 付款 → 查詢訂單。',
      無法完成付款怎麼辦: '請確認信用卡狀態，或改以 ATM / 超商付款。',
      一筆訂單最多可購買幾張: '每場次限制不同，以活動頁公告為準。',
    },
  },
  collection: {
    name: '取票',
    desc: '取票方式說明',
    faqs: {
      超商取票方式: '至 7-11 / 全家 的機台輸入取票代碼即可取票。',
      電子票如何使用: '於入場時出示手機電子票 QR Code。',
    },
  },
  business: {
    name: '交易票券',
    desc: '票券交易與轉讓說明',
    faqs: {
      如何轉讓票券: '請依照活動規範，在會員中心執行票券轉讓流程。',
      票券是否可退: '依照活動主辦規定，如無特別公告，通常不可退票。',
    },
  },
  query: {
    name: '查詢訂單',
    desc: '查詢訂單方式說明',
    faqs: {
      如何找到我的訂單: '請至會員中心 → 訂單查詢查看所有訂單紀錄。',
      '訂單找不到？': '請確認登入帳號是否正確。',
    },
  },
  other: {
    name: '其他',
    desc: '其他類別說明',
    faqs: {
      忘記密碼: '可使用「忘記密碼」功能重設密碼。',
      變更會員資料: '可至會員中心修改個人資料。',
    },
  },
  contactUs: {
    name: '聯繫我們',
    desc: '尋找協助',
    faqs: {
      客服聯繫方式: '若遇問題可使用客服表單聯繫拓元。',
    },
  },
};

// ==========================
// Main Component
// ==========================
export default function SupportDetailPage() {
  const router = useRouter();
  const searchParams = useSearchParams();
  const key = Array.from(searchParams.keys())[0]; // 取得 like ?purchase 的 key
  const [selectedFaq, setSelectedFaq] = useState(null);

  const data = SUPPORT_MAP[key];

  // 若 query 不存在，導回首頁
  useEffect(() => {
    if (!data) router.push('/');
  }, [data]);

  return (
    <ThemeProvider theme={theme}>
      <PageContainer>
        {/* Header */}
        <HeaderBar>{data?.name}</HeaderBar>

        {/* 如果正在看 FAQ 詳細內容 */}
        {selectedFaq ? (
          <>
            <BackBar>
              <IconButton onClick={() => setSelectedFaq(null)}>
                <ArrowBackIosNewIcon sx={{ color: theme.palette.primary.main, fontSize: 18 }} />
              </IconButton>
              <Typography sx={{ fontWeight: 600 }}>{selectedFaq}</Typography>
            </BackBar>

            <Box sx={{ p: 3 }}>
              <Typography variant='body1' sx={{ lineHeight: 1.7 }}>
                {data.faqs[selectedFaq]}
              </Typography>
            </Box>
          </>
        ) : (
          <>
            {/* Section Header */}
            <SectionHeader>
              <Typography variant='h5' sx={{ fontWeight: 700, color: theme.palette.primary.main }}>
                {data?.name}
              </Typography>
              <Typography variant='subtitle1'>{data?.desc}</Typography>
            </SectionHeader>

            {/* FAQ 列表 */}
            {Object.keys(data?.faqs || {}).map((title, idx) => (
              <AccordionStyled key={idx}>
                <AccordionSummary expandIcon={<ExpandMoreIcon />}>
                  <Typography sx={{ fontWeight: 600 }}>{title}</Typography>
                </AccordionSummary>
                <AccordionDetails>
                  <Typography
                    sx={{
                      color: theme.palette.primary.main,
                      cursor: 'pointer',
                      '&:hover': { textDecoration: 'underline' },
                    }}
                    onClick={() => setSelectedFaq(title)}
                  >
                    查看詳細內容 →
                  </Typography>
                </AccordionDetails>
              </AccordionStyled>
            ))}
          </>
        )}
      </PageContainer>
    </ThemeProvider>
  );
}
